.sidebar-navbar {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(1, 1, 1, 0.5);
  height: 100vh;
  width: 57px;
}

.sidebar-navbar-container {
  padding-top: 30px;
}

.sidebar-nav-link {
  display: block;
  color: #456571;
  text-decoration: none;
  font-size: 1.25rem;
  padding: 0.5rem;
  transition: color 0.3s ease;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
}

.sidebar-nav-link:hover,
.active-link {
  color: white;
  font-weight: bold;
  text-decoration: none;
  cursor: pointer;
}


/* Contact Info - Positioned at the bottom */
.contact-info {
  display: flex;
  flex-direction: column; /* Stack the icons vertically */
  align-items: center;
  position: absolute;
  bottom: 0.5rem; /* Positioning it at the bottom of the sidebar */
  width: 100%;
}

/* Styling for individual contact icons */
.contact-icon {
  color: #456571;
  font-size: 1rem;
  margin: 0.5rem 0; /* Space between the icons */
  transition: color 0.3s ease;
}

.contact-icon:hover {
  color: white; /* Change color on hover */
}

@media (max-width: 768px) {
  .sidebar-navbar {
    width: 40px; /* Make sidebar narrower */
  }
  
  /* Reduce font size of nav links for mobile */
  .sidebar-nav-link {
    font-size: 1rem; /* Smaller font size */
    padding: 0.3rem; /* Less padding */
  }

  /* Adjust the position of contact info at the bottom */
  .contact-info {
    margin-top: 2rem; /* Increase space between navigation and icons */
    position: relative; /* Ensure the icons don't overlap */
    height: 10px; 
  }
}