/* App.css */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: url('/public/background2.jpg') no-repeat center center fixed;
  background-size: contian;
  display: flex;
  flex-direction: column;
  justify-content: center;  /* This centers your content vertically */
  align-items: center;  /* This centers your content horizontally */
}


.app-container {
  display: flex;
}

.main-content {
  margin-left: 57px;
  width: 100%;
  overflow-x: hidden;
}

.section{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5%;
}


@media (max-width: 768px) {

  body {

    background-color: black;
    margin: 0;
    font-family: Arial, sans-serif;
  }

  .main-content{
    margin-left: 40px;
  }
}